import { _decorator, Component, instantiate, Prefab, Vec3, tween} from 'cc';
const { ccclass, property } = _decorator;

@ccclass('FallingBlocks')
export class FallingBlocks extends Component {
    @property({ type: Prefab })
    blockPrefab: Prefab | null = null;

    start() {
        
        tween(this.node).to(1,{position:new Vec3(0,0,0)})
        .call(() => {
            // Khi di chuyển kết thúc, hủy bỏ khối
            this.node.destroy();
        })
        .start();
    }

   
}
